import React from 'react';



const Projects = (props) => {


    const enable = {
        opacity : "1",
        transitionDuration : "0.7s",
        position: 'fixed',
        right: '20rem',
        left: '20rem',
        top: '10rem',
        bottom: '10rem',
        borderRadius: '30px',
        padding: '2rem 3rem',
        boxShadow: '0 20px 20px rgba(0, 0, 0, 0.2), 0px 0px 50px rgba(0, 0, 0, 0.2)'
    };
    const disable = {
        position:'fixed',
        top:'-100rem',
        left: '20rem',
        right:'20rem',
        opacity :  "0",
        transitionDuration : "0.7s",
        height : '0'
    };
    
    return(
        <div style={props.opacityProject ? enable : disable}>
                <p style={pstyle}>Vos donnes personnelles seront traites de faon strictement confidentielle et ne seront en aucun cas transmises  des tiers.</p>  
                <div style ={baseStyle}>Descriptif du projet  raliser:
                    <div>
                        <textarea style={Field} id="textarea" />
                    </div>
                </div>
                <div style ={baseStyle}>Dlais de livraison:<input></input></div>
                <p style={pstyle}></p>Merci de bien importer un fichier .Zip contenant les lments ncessaires  l'tudes de votre projet.
                <p style={pstyle}>Votre projet sera tudi dans un dlai de 48 heures aprs dpts de celui-ci. Un devis vous sera envoy  l'adresse suivante : "" 
                aprs tudes de projet et capacit de livraison. Vous pourrez retrouver dans votre espace client vos fichiers dposer ainsi que le devis.
                Dans le cas d'un possible accord, vous allez aussi pouvoir suivre l'tat d'avancement sur ce mme dpt en ligne.</p>
        </div>
    )
}
export default Projects;


const baseStyle = {
};


const pstyle = {
    color:'black',
    cursor : 'default',
};



const Field = {
    width:"100%",
    height:"10vh",
    resize: 'none'
};